import lunr from "lunr";

import jquery from 'jquery';
window.jQuery = window.$ = jquery;

import 'bootstrap';


$(document).ready(function() {

  const idx = lunr(function () {
    this.field("title");
    this.field("summary");
    this.field("tags");
    this.ref('ref');
    documents.forEach(function (doc) {
      this.add(doc);
    }, this);
  });

  let $input = $('#lunr_search_input');
  let ul = $('#search-results ul');

  $input.keyup(function () {
    let value = $(this).val();
    let results = [];
    if (value.length < 5) {
      value = value + '*';
    }
    if (value.length > 3) {
      results = idx.search(value);
    }
    $ul.html('');
    results.forEach(function(hit) {
      let content = documents[parseInt(hit.ref)];
      ul.append(`<li class='list-group-item'><a href='${content.url}'>${content.title}</a></li>`);
    });
    if (results) {
      $input.dropdown('show');
    } else {
      $input.dropdown('hide');
    }
  });
});

